<template>
  <div id="app">
    <AuthForm v-if="!user" />
    <div v-else class="calendar-container">
      <header class="app-header">
        <!-- Áõ∏Á∞øÂàáÊèõ + Êñ∞Â¢û + Âà™Èô§ -->
        <div class="album-switch">
          <select v-model="selectedIndex">
            <option
              v-for="(a, i) in albums"
              :key="i"
              :value="i"
            >{{ a }}</option>
          </select>
          <button
            class="add-btn"
            @click="addAlbum"
            title="Êñ∞Â¢ûÁõ∏Á∞ø"
          >Ôºã</button>
          <button
            class="delete-btn"
            :disabled="albums.length === 1"
            @click="deleteAlbum"
            title="Âà™Èô§Áõ∏Á∞ø"
          >üóëÔ∏è</button>
        </div>

        <!-- ÁõÆÂâçÁõ∏Á∞øÊ®ôÈ°å & Á∑®ËºØ -->
        <div v-if="!editing" class="title-display">
          <h1 class="app-title">{{ albums[selectedIndex] }}</h1>
          <button
            class="edit-btn"
            @click="startEdit"
            title="Á∑®ËºØÁõ∏Á∞øÂêçÁ®±"
          >‚úèÔ∏è</button>
        </div>
        <div v-else class="title-edit">
          <input
            v-model="tempName"
            type="text"
            class="title-input"
            placeholder="Ëº∏ÂÖ•Áõ∏Á∞øÂêçÁ®±"
          />
          <button
            class="save-btn"
            @click="saveEdit"
            title="ÂÑ≤Â≠òËÆäÊõ¥"
          >‚úîÔ∏è</button>
          <button
            class="cancel-btn"
            @click="cancelEdit"
            title="ÂèñÊ∂à"
          >‚úñÔ∏è</button>
        </div>

        <button
          @click="logout"
          class="logout-btn"
        >ÁôªÂá∫</button>
      </header>

      <CalendarView :album="albums[selectedIndex]" />
    </div>
  </div>
</template>

<script setup>
import { ref, onMounted } from 'vue'
import { onAuthStateChanged, signOut } from 'firebase/auth'
import { auth } from './firebase.js'

import AuthForm     from './components/AuthForm.vue'
import CalendarView from './components/CalendarView.vue'

// --- Firebase Auth ---
const user = ref(null)
onMounted(() => onAuthStateChanged(auth, u => (user.value = u)))
function logout() { signOut(auth) }

// --- Áõ∏Á∞øÂàóË°® & ÂàáÊèõ/Êñ∞Â¢û/Âà™Èô§/Á∑®ËºØ ---
const albums        = ref(['Áõ∏Á∞øÊó•ÊõÜ'])
const selectedIndex = ref(0)
const tempName      = ref('')
const editing       = ref(false)

/** Êñ∞Â¢û‰∏ÄÂÄãÁõ∏Á∞ø */
function addAlbum() {
  const name = prompt('Ë´ãËº∏ÂÖ•Êñ∞Áõ∏Á∞øÂêçÁ®±Ôºö')?.trim()
  if (name && !albums.value.includes(name)) {
    albums.value.push(name)
    selectedIndex.value = albums.value.length - 1
  }
}

/** Âà™Èô§Áï∂ÂâçÁõ∏Á∞øÔºå‰∏¶Ê∏ÖÈô§ localStorage */
function deleteAlbum() {
  const idx = selectedIndex.value
  const name = albums.value[idx]
  if (!confirm(`Á¢∫ÂÆöË¶ÅÂà™Èô§Áõ∏Á∞ø„Äå${name}„ÄçÂóéÔºüÊ≠§Êìç‰ΩúÊúÉÁßªÈô§ÊâÄÊúâÂúñÁâáË≥áÊñô„ÄÇ`)) {
    return
  }
  // 1. Ê∏Ö localStorage
  const prefix = `photoDiary__${name}__`
  Object.keys(localStorage)
    .filter(key => key.startsWith(prefix))
    .forEach(key => localStorage.removeItem(key))

  // 2. Âæû albums ÂàóË°®Âà™Èô§
  albums.value.splice(idx, 1)

  // 3. Ë™øÊï¥ selectedIndex
  if (idx >= albums.value.length) {
    selectedIndex.value = albums.value.length - 1
  }
}

/** Á∑®ËºØÁõ∏Á∞øÂêçÁ®± */
function startEdit() {
  tempName.value = albums.value[selectedIndex.value]
  editing.value = true
}
function saveEdit() {
  const v = tempName.value.trim()
  if (v) {
    // ÈÄ£Âêå localStorage prefix ‰πüË¶ÅÈáçÂëΩÂêç
    const oldName = albums.value[selectedIndex.value]
    const newName = v
    const oldPrefix = `photoDiary__${oldName}__`
    const newPrefix = `photoDiary__${newName}__`
    // ÈáçÊñ∞ÂëΩÂêç localStorage key
    Object.keys(localStorage).forEach(key => {
      if (key.startsWith(oldPrefix)) {
        const data = localStorage.getItem(key)
        localStorage.removeItem(key)
        const newKey = key.replace(oldPrefix, newPrefix)
        localStorage.setItem(newKey, data)
      }
    })
    // Êõ¥Êñ∞ÂàóË°®
    albums.value[selectedIndex.value] = newName
  }
  editing.value = false
}
function cancelEdit() {
  editing.value = false
}
</script>

